.home-image{
    background:linear-gradient(to bottom,rgba(54, 54, 54, 0.3),rgba(61, 51, 28, 0.3)),url("../../../assets/main-home-page.jpg");
    background-size: cover;
    width: 100%;
    height: 800px;
}
.home-title{
    color: white;
    font-size: 6rem;
    text-align: center;
    margin-top: 0;
    font-family: 'EB Garamond', serif;
    padding-top: 220px;
    letter-spacing: 20px;
}
.home-Services{
    height: 700px;
    display: flex;
}
.home-service-image{
    background: url('../../../assets/main-home-service.jpg');
    height: 570px;
    width: 100%;
    background-size: 100% 570px;
  margin-top:3.2rem ;
  margin-left: 4rem;
  margin-right: 6rem;
}
.home-service-detail{
    width: 40%;
    margin-top: 50px;
    margin-left: 100px;
    text-align: center;
    background-color: #AF9453;
    height: 400px;
    color: white;
    padding-top: 10%;
    font-family: 'Roboto', sans-serif;
   
}
.home-services-title{
    letter-spacing: 10px;
    font-size: 2.3rem;

}
.home-service-paragraph{
    margin-top: 6rem;
    font-family: 'Courier New', Courier, monospace;
    font-size: 1.4rem;
}
.home-service-button{
    width: 350px;
    height: 60px;
    background-color: #AF9453;
    color: white;
    border: 1px solid white;
    cursor: pointer;
    margin-top: 3rem;
}
.home-service-button:hover{
    color: #AF9453;
border: 1px solid white;
background: white;
}
.main-gallery{
    padding-right: 6rem;
    padding-left: 6rem;
    padding-bottom: 4rem;
}
.home-service-gallery{
    background: url("../../../assets/main-home-gallery.jpg");
    width: 100%;
    height: 800px;
    background-size: cover;

}
.main-gallery-details{
    height: 600px;
    background-color: #AF9453;
    width: 400px;
    color: white;
    font-family: 'Roboto', sans-serif;
    margin-left: 980px;
}
.main-gallery-details-title{
    text-align: center;
    padding-top: 100px;
    font-size: 2.5rem;
    letter-spacing: 10px;
}  
.main-gallery-details-paragraph{
    text-align: center;
    padding-top: 90px;
    font-size: 1.4rem;
    font-family: 'Courier New', Courier, monospace;
}
.main-gallery-details-button{
    margin-left: 10%;
    width: 330px;
    height: 60px;
    margin-top: 70px;
    background-color: #AF9453;
    color: white;
    border: 1px solid white;
}
.main-gallery-details-button:hover{
    background-color: white;
color: #AF9453;
}