.input-form{
    margin: 160px 40%;
}
.input-title{
    text-align: center;
    color: #AF9453;
    margin-left: 40px;
    margin-bottom: 40px;
}
.input-name{
    display: block;
    width: 100%;
    height: 2rem;
    margin: 20px;
    border: 1px solid #AF9453;
}
.input-email{
    display: block;
    width: 100%;
    height: 2rem;
    margin: 20px;
    border: 1px solid #AF9453;
}
.input-phone{
    display: block;
    width: 100%;
    height: 2rem;
    margin: 20px;
    border: 1px solid #AF9453;
}
.input-button{
    display: block;
    width: 102.5%;
    height: 2.2rem;
    margin: 20px;
    border: 1px solid #AF9453;
    background-color: #AF9453;
    color: white;
}
.input-name:focus{
    outline: none;
}
.input-email:focus{
    outline: none;
}
.input-phone:focus{
    outline: none;
}
.input-button:focus{
    outline: none;
}
.input-button:hover{
background-color: white;
color: #AF9453;
}