.service-background{
    background-color: #AF9453;
margin-bottom: 200px;
    height: 600px;
}
.service-background-image{
    position: absolute;
    width: 60%;
    height: 600px;
    top: 220px;
    left:  20%;
}
.service-list{
  padding-left: 18%;
  padding-bottom: 5%;
}
.service-list h1{
    width: 700px;
    margin: 0;
    display: inline-block;
    font-family: 'Roboto', sans-serif;
    font-weight: 100;
}
.service-list p{
    width: 300px;
    margin: 0;
    display: inline-block;
    font-family: 'Roboto', sans-serif;
}
.service-list button{
    width: 130px;
    height: 40px;
    background-color: #AF9453;
    color: white;
    border: 2px solid #AF9453;
}
.service-list button:focus{
    outline: none;
}
.service-list button:hover{
    background-color: white;
    color: #AF9453;
}